import axios from 'axios';
import CryptoJS from 'crypto-js/crypto-js'
/** 
 * @method restful
 * @param api
 * @param {string} api.url 
 * @param {string} api.type  get post put delete
 * @param {boolean} api.placeholder  true false url
 * @param {boolean} api.formData formData true false formDatapost
 * @param {object}request  
 */

export function server(api, request = {}) {
    /** 
     * 
     */
    // 
    if (api.placeholder) {
        for (var i in request) {
            if (api.url.indexOf(`{${i}}`) != -1) {
                api.url = api.url.replace(`{${i}}`, `${request[i]}`);
                delete request[i]
            }
        }
    }
    // promise
    return new Promise((resolve, reject) => {
        // -150000
        let timeOut = setTimeout(() => {
            console.error('timeout', '');
            reject('')
        }, 15000)
        // get
        if (api.type == "get") {
            // axios
            return axios({
                    // 
                    url: api.url,
                    // 
                    method: 'get',
                    // url
                    params: {
                        ...request
                    },
                    // 
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                    },
                })
                .then(function (response) {
                    // 
                    clearTimeout(timeOut);
                    // resolve
                    resolve(response.data);
                })
                .catch(function (error) {
                    // reject
                    reject(error);
                    // 
                    console.error('error', error);
                });
        } else
            // post
            if (api.type == "post") {
                // formData
                if (api.formData && api.type == "post") {
                    const formdata = new FormData();
                    for (let i in request) {
                        formdata.append(i, request[i])
                    }
                    request = formdata;
                }
                // axios
                return axios({
                        // 
                        url: api.url,
                        // 
                        method: 'post',
                        // body
                        data: request,
                        // 
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8'
                        },
                    })
                    .then(function (response) {
                        // 
                        clearTimeout(timeOut);
                        // resolve
                        resolve(response.data);
                    })
                    .catch(function (error) {
                        // reject
                        reject(error);
                        // 
                        console.error('error', error);
                    });
            } else
                // put
                if (api.type == "put") {
                    // axios
                    return axios({
                            // 
                            url: api.url,
                            // 
                            method: 'put',
                            // body
                            params: {},
                            // body
                            data: request,
                            // 
                            headers: {
                                'Content-Type': 'application/json;charset=utf-8'
                            },
                        })
                        .then(function (response) {
                            // 
                            clearTimeout(timeOut);
                            // resolve
                            resolve(response.data);
                        })
                        .catch(function (error) {
                            // reject
                            reject(error);
                            // 
                            console.error('error', error);
                        });
                } else
                    // delete
                    if (api.type === "delete") {
                        // axios
                        return axios({
                                // 
                                url: api.url,
                                // 
                                method: 'delete',
                                // url
                                params: {
                                    ...request
                                },
                                // 
                                headers: {
                                    'Content-Type': 'application/json;charset=utf-8'
                                },
                            })
                            .then(function (response) {
                                // 
                                clearTimeout(timeOut);
                                // resolve
                                resolve(response.data);
                            })
                            .catch(function (error) {
                                // reject
                                reject(error);
                                // 
                                console.error('error', error);
                            });
                    }
    })
}
export default server;



const crypto = require('crypto');
 
export function aesEncrypt(data, key) {
    const cipher = crypto.createCipher('aes192', key);
    var crypted = cipher.update(data, 'utf8', 'hex');
    crypted += cipher.final('hex');
    return crypted;
}
 //
export function aesDecrypt(encrypted, key) {
    const decipher = crypto.createDecipher('aes192', key);
    var decrypted = decipher.update(encrypted, 'hex', 'utf8');
    decrypted += decipher.final('utf8');
    return decrypted;
}
